.mainright-wrapper {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 0 5%;
    /* border: 1px solid lime; */
}

.mainright-top {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 34%;
    /* border: 1px solid wheat; */
}

.mainright-top-title {
    box-sizing: border-box;
    margin: 0 auto;
    width: 55%;
    height: 35px;
    text-align: center;
    line-height: 35px;
    background-image: url(../../statics/img1/itemtitle.png);
    background-size: 100% 100%;
    /* border: 1px solid lightsalmon; */
}

.mainright-top-item-1 {
    box-sizing: border-box;
    position: absolute;
    top: 25%;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-around;
    /* border: 1px solid fuchsia; */
}
.mainright-top-item-1-img {
    box-sizing: border-box;
    margin-left: 10px;
    width: 60px;
    height: 60px;
    background-image: url(../../statics/img1/drsjxl.png);
    background-size: 100% 100%;
    /* border: 1px solid steelblue; */
}
.mainright-top-item-1-wrapper {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 10px;
    /* border: 1px solid forestgreen; */
    width: calc(100% - 70px) ;
    height: 100%;
}
.mainright-top-item-2 {
    box-sizing: border-box;
    position: absolute;
    top: 60%;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-around;
    /* border: 1px solid fuchsia; */
}
.mainright-top-item-2-img {
    box-sizing: border-box;
    margin-left: 10px;
    width: 60px;
    height: 60px;
    background-image: url(../../statics/img1/dnljxl.png);
    background-size: 100% 100%;
    /* border: 1px solid steelblue; */
}


/* 中部样式 */
.mainright-middle {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: calc(33% ) ;
    /* border: 1px solid hotpink; */
}
.mainright-middle-title {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    background-image: url(../../statics/img1/youbiaoti.png);
    text-align: center;
    line-height: 40px;
    background-size: 100% 100%;
    /* border: 1px solid hotpink; */
}

.mainright-bottom {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: calc(33%) ;
    /* border: 1px solid red; */
}


/* 通用样式 */
.huise-color-text {
    color: rgb(131, 131, 131);
    font-size: 12px;
}
.big-weight-color-text {
    color: #ffffff;
    font-size: 20px;
    font-weight: 600;
}