.MainLeftBottomItem-wrapper {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    /* border: 1px solid royalblue; */
    overflow: hidden;
}
.MainLeftBottomItem-top-title {
    box-sizing: border-box;
    margin: 0 auto;
    width: 55%;
    height: 35px;
    text-align: center;
    font-weight: 700;
    line-height: 35px;
    background-image: url(../../statics/img1/itemtitle.png);
    background-size: 100% 100%;
    /* border: 1px solid lightsalmon; */
}
.MainLeftBottomItem-middle-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10% 0 10% 0;
    box-sizing: border-box;
    width: 100%;
    height: 45%;
    /* border: 1px solid khaki; */
}
.MainLeftBottomItem-middle-wrapper-top {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    height: 50%;
    /* border: 1px solid darkorange; */
}
.MainLeftBottomItem-middle-wrapper-top-top {
    font-size: 12px;
    text-align: center;
    color: rgb(146, 146, 146);
}
.MainLeftBottomItem-middle-wrapper-top-bottom {
    text-align: center;
    color: #ffffff;
    font-size: 20px;
    font-weight: 600;
}

.MainLeftBottomItem-middle-wrapper-bottom {
    display: flex;
    width: 100%;
    height: 40%;
    /* border: 1px solid darkorchid; */
}
.MainLeftBottomItem-middle-wrapper-bottom-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    text-align: center;
    /* border: 1px solid darkseagreen; */
}
.border-right-1{
    border-right: 1px solid rgb(146, 146, 146);
}

.MainLeftBottomItem-middle-wrapper-bottom-item-bottom {
    display: flex;
    justify-content: center;
    /* border: 1px solid deeppink; */
}
.MainLeftBottomItem-middle-wrapper-bottom-item-bottom-bottom-up {
    width: 12px;
    height: 12px;
    margin: 0px 0px 0px 5px;
    background-image: url(../../statics/img1/zengzhangshang.png);
    background-size: 100% 100%;
}
.MainLeftBottomItem-middle-wrapper-bottom-item-bottom-bottom-down {
    width: 12px;
    height: 12px;
    margin: 0px 0px 0px 5px;
    background-image: url(../../statics/img1/zengzhangxia.png);
    background-size: 100% 100%;
}

.MainLeftBottomItem-bottom-wrapper {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 50%;
    /* border: 1px solid red; */
}
.MainLeftBottomItem-bottom-wrapper-top {
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding: 0px 10px;
    background-image: url(../../statics/img1/licaixiaobiaoti.png);
    background-size: 100% 100%;
    /* border: 1px solid navajowhite; */
}
.MainLeftBottomItem-bottom-wrapper-top-right {
    position: absolute;
    display: flex;
    justify-content: space-around;
    top: 0;
    right: 10px;
    width: 30px;
    height: 30px;
    /* border: 1px solid royalblue; */
}
.MainLeftBottomItem-bottom-wrapper-top-right-item-0ff {
    width: 6px;
    height: 6px;
    border-radius: 100%;
    cursor: pointer;
    background-color: rgb(0, 255, 255);
    margin-top: 13px;
}
.MainLeftBottomItem-bottom-wrapper-top-right-item-hui {
    width: 6px;
    height: 6px;
    border-radius: 100%;
    cursor: pointer;
    background-color: rgb(61, 63, 96);
    margin-top: 13px;
}

.MainLeftBottomItem-bottom-wrapper-slider {
    position: relative;
    top: 0;
    left: 0%;
    box-sizing: border-box;
    display: flex;
    width: 200%;
    height: calc(100% - 30px);
    /* border: 1px solid green; */
    transition: left 1s;
}
.MainLeftBottomItem-echarts-wrapper-1 {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 50%;
    height: calc(100%);
    /* border: 1px solid red; */
}
.MainLeftBottomItem-echarts-wrapper-2 {
    position: absolute;
    top: 0%;
    left: 50%;
    width: 50%;
    height: calc(100%);
    /* border: 1px solid red; */
}
.aa {
    background-color: salmon;
}
.bb {
    background-color: seagreen;
}