 .App {
   position: relative;
   box-sizing: border-box;
   background-image: url(./statics/img1/bg.png);
   background-size: 100% 100%;
   width: 100%;
   height: 100%;
   padding: 1%;
   color: #fff;
 }

 /* 头部样式 */

 .app-headertop-wrapper {
   position: relative;
   box-sizing: border-box;
   width: 100%;
   height: 10%;
   /* border: 1px solid firebrick; */
 }

 /* 主体样式 */
 .app-main-wrapper {
   position: relative;
   box-sizing: border-box;
   width: 100%;
   height: 90%;
   /* border: 1px solid forestgreen; */
 }

 /* 主体左边样式 */
 .app-main-wrapper-mainleft-wrapper {
   position: absolute;
   top: 0;
   left: 0;
   box-sizing: border-box;
   display: inline-block;
   width: 83%;
   height: 100%;
   /* border: 1px solid yellow; */
 }

 /* 主体左边上部样式 */
 .app-main-wrapper-mainleft-wrapper-top {
   position: relative;
   width: 100%;
   height: 49%;
   box-sizing: border-box;
   background-image: url(./statics/img1/dbbg.png);
   background-size: 100% 100%;
   /* border: 1px solid indianred; */
 }

 .jiaozs {
   position: absolute;
   top: 0px;
   left: 0px;
   width: 25px;
   height: 25px;
   background-image: url(./statics/img1/jiaozs.png);
   background-size: 100% 100%;
   z-index: 10;
 }

 .jiaozx {
   position: absolute;
   bottom: 0px;
   left: 0px;
   width: 25px;
   height: 25px;
   background-image: url(./statics/img1/jiaozx.png);
   background-size: 100% 100%;
   z-index: 10;
 }

 .jiaoys {
   position: absolute;
   top: 0px;
   right: 0px;
   width: 25px;
   height: 25px;
   background-image: url(./statics/img1/jiaoys.png);
   background-size: 100% 100%;
   z-index: 10;
 }

 .jiaoyx {
   position: absolute;
   bottom: 0px;
   right: 0px;
   width: 25px;
   height: 25px;
   background-image: url(./statics/img1/jiaoyx.png);
   background-size: 100% 100%;
   z-index: 10;
 }

 /* 主体左边下部样式 */
 .app-main-wrapper-mainleft-wrapper-bottom {
   position: relative;
   width: 100%;
   height: 51%;
   box-sizing: border-box;
   padding-top: 20px;
   /* border: 1px solid indianred; */
 }

 /* 主体右边样式 */
 .app-main-wrapper-mainright-wrapper {
   position: absolute;
   top: 0;
   right: 0;
   box-sizing: border-box;
   display: inline-block;
   width: 16%;
   height: 100%;
   background-image: url(./statics/img1/1you.png);
   background-size: 100% 100%;
   /* border: 1px solid goldenrod; */
 }