.headertop-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.headertop-img-wrapper {
    width: 100%;
    height: 100%;
    background-image: url(../../statics/img1/title.png);
    background-size: 100% 100%;
    font-size: 28px;
    color: #ffffff;
    font-weight: 600;
}
.headertop-title {
    position: absolute;
    display: inline-block;
    width: 20%;
    text-align: center;
    top: 27%;
    left: 50%;
    transform: translateX(-50%);
    /* border: 1px solid lawngreen; */
}

.headertop-danwei {
    position: absolute;
    right: 10px;
    bottom: 16px;
    display: inline-block;
    font-size: 12px;
    color: rgb(146, 146, 146);
    /* border: 1px solid lawngreen; */
}
.headertop-tip-wrapper {
    position: absolute;
    display: flex;
    padding: 4px;
    background-image: url(../../statics/img1/xykbg.png);
    background-size: 100% 100%;
    right: 90px;
    bottom: 12px;
    font-size: 12px;
    font-weight: 400;
    /* border: 1px solid lawngreen; */
}
.headertop-tip-wrapper-img {
    width: 14px;
    height: 14px;
    background-image: url(../../statics/img1/gth.png);
    background-size: 100% 100%;
}
