.MainLeftItem-wrapper {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 0px 3%;
    overflow: hidden;
    /* border: 1px solid lime; */
}
.MainLeftItem-top-title {
    position: absolute;
    top: 3%;
    left: 5%;
    box-sizing: border-box;
    margin: 0 auto;
    width: 30%;
    height: 50px;
    font-weight: 600;
    text-align: center;
    font-size: 20px;
    line-height: 50px;
    background-image: url(../../statics/img1/lefttitle.png);
    background-size: 100% 100%;
    /* border: 1px solid lightsalmon; */
}
.MainLeftItem-middle-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 15% 0 5% 0;
    box-sizing: border-box;
    width: 100%;
    height: 40%;
    /* border: 1px solid yellow; */
}
.MainLeftItem-middle-wrapper-top {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 50%;
    box-sizing: border-box;
    /* border: 1px solid fuchsia; */
}
.MainLeftItem-middle-wrapper-top-left {
    position: absolute;
    top: 0;
    left: 0;
    width: 30%;
    height: 100%;
    /* border: 1px solid red; */
}
.MainLeftItem-middle-wrapper-top-left-item {
    position: absolute;
    top: 50%;
    left: 0%;
    transform: translateY(-50%);
    background-image: url(../../statics/img1/lefttitle.png);
    background-size: 100% 100%;
    padding: 2px 0;
    width: 100%;
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-weight: 600;
    font-size: 20px;
    /* border: 1px solid palegreen; */
}
.MainLeftItem-middle-wrapper-top-middle {
    position: absolute;
    top: 0;
    left: 30%;
    text-align: center;
    line-height: 100%;
    width: 40%;
    height: 100%;
    font-size: 20px;
    font-weight: 600;
    color: rgb(0, 255, 255);
    /* border: 1px solid deeppink; */
}
.MainLeftItem-middle-wrapper-top-middle-item {
    position: absolute;
    top: 50%;
    left: 0%;
    transform: translateY(-50%);
    width: 100%;
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-weight: 600;
    font-size: 26px;
    /* border: 1px solid palegreen; */
}

.MainLeftItem-middle-wrapper-top-right {
    position: absolute;
    top: 0;
    right: 0%;
    width: 30%;
    height: 100%;
    /* border: 1px solid darksalmon; */
}
.MainLeftItem-middle-wrapper-top-right-item {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-image: linear-gradient(to right, rgb(0,145,248) , rgb(0,95,200));
    padding: 2px 8px;
    border-radius: 30px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    font-size: 14px;
    /* border: 1px solid palegreen; */
}

.MainLeftItem-middle-wrapper-bottom {
    position: absolute;
    top: 50%;
    left: 0%;
    width: 100%;
    height: 50%;
    box-sizing: border-box;
    /* border: 1px solid royalblue; */
}
.MainLeftItem-middle-wrapper-bottom-left {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items:center;
    box-sizing: border-box;
    width: 50%;
    height: 100%;
    /* border: 1px solid aqua; */
}
.MainLeftItem-middle-wrapper-bottom-left-left {
    box-sizing: border-box;
    padding: 2px 10px;
    color: #929292;
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    /* border: 1px solid aqua; */
}
.MainLeftItem-middle-wrapper-bottom-left-middle {
    box-sizing: border-box;
    height: 20px;
    line-height: 20px;
    padding: 2px 10px;
    /* border: 1px solid palegreen; */
}
.MainLeftItem-middle-wrapper-bottom-left-right {
    width: 15px;
    height: 15px;
    box-sizing: border-box;

    /* border: 1px solid olivedrab; */
}
.zhengzhangshang {
    background-image: url(../../statics/img1/zengzhangshang.png);
    background-size: 100% 100%;
}
.zhengzhangxia {
    background-image: url(../../statics/img1/zengzhangxia.png);
    background-size: 100% 100%;
}

.MainLeftItem-middle-wrapper-bottom-right {
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items:center;
    box-sizing: border-box;
    width: 50%;
    height: 100%;
    /* border: 1px solid aqua; */
}

.MainLeftItem-bottom-wrapper {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 60%;
    /* border: 1px solid red; */
}
.MainLeftItem-bottom-wrapper-top {
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding: 0px 10px;
    background-color: #000;
    /* background-image: url(../../statics/img1/licaixiaobiaoti.png);
    background-size: 100% 100%; */
    /* border: 1px solid navajowhite; */
}
.MainLeftItem-bottom-wrapper-top-right {
    position: absolute;
    display: flex;
    justify-content: space-around;
    top: 0;
    right: 10px;
    width: 30px;
    height: 30px;
    /* border: 1px solid royalblue; */
}
.MainLeftItem-bottom-wrapper-top-right-item-0ff {
    width: 6px;
    height: 6px;
    border-radius: 100%;
    cursor: pointer;
    background-color: rgb(0, 255, 255);
    margin-top: 13px;
}
.MainLeftItem-bottom-wrapper-top-right-item-hui {
    width: 6px;
    height: 6px;
    border-radius: 100%;
    cursor: pointer;
    background-color: rgb(61, 63, 96);
    margin-top: 13px;
}
.MainLeftItem-echarts-wrapper {
    width: 100%;
    height: calc(100% - 30px);
    /* border:1px solid red; */
}

.MainLeftItem-zhanwei {
    position: absolute;
    border: 0.8px solid #969696;
    height: 25px;
    left: 50%;
    top: 50%;
    transform: translateY(-50%);
}

.MainLeftItem-bottom-wrapper-slider {
    position: relative;
    top: 0;
    left: 0%;
    box-sizing: border-box;
    display: flex;
    width: 200%;
    height: calc(100% - 30px);
    /* border: 1px solid green; */
    transition: left 1s;
}
.MainLeftItem-echarts-wrapper-1 {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 50%;
    height: calc(100%);
    /* border: 1px solid red; */
}
.MainLeftItem-echarts-wrapper-2 {
    position: absolute;
    top: 0%;
    left: 50%;
    width: 50%;
    height: calc(100%);
    /* border: 1px solid red; */
}